import React, { useState, useEffect } from "react";
import Sidebar from "../../components/Admin/Sidebar";
import TopBar from "../../components/Admin//TopBar";
import DataTable from "react-data-table-component";
import {
  Eye,
  Trash2,
  Edit,
  Lightbulb,
  Building2,
  UserCheck,
  Component,
  Presentation,
  BookText,
  ShieldOff,
} from "lucide-react";
import { useNavigate, Link } from "react-router-dom";
import { FaEye } from "react-icons/fa";
import axios from "axios";
function AdminCompany() {
  const navigate = useNavigate();
  const [isOpen, setIsOpen] = useState(false);
  const [show, setShow] = useState(false);
  const [successMessage, setsuccessMessage] = useState("");
  const [deleteId, setdeleteId] = useState(false);
  const [showEdit, setshowEdit] = useState(false);
  const [editdata, seteditdata] = useState("");
  const [spinners, setspinnsers] = useState(false);
  const toggleNavAdmin = () => {
    setIsOpen(!isOpen); // Toggle the state between true and false
  };
  const apiUrl = "https://blueprintcatalyst.com/api/admin/module/";
  const [records, setRecords] = useState([]);
  document.title = "All Company - Admin";
  useEffect(() => {
    getallcompnay();
  }, []);
  const getallcompnay = async () => {
    let formData = {
      user_id: "",
    };
    try {
      const res = await axios.post(apiUrl + "getallcompnay", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json", // Ensure the content type is JSON
        },
      });
      var respo = res.data.results;
      console.log(respo);
      setRecords(respo);
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };
  const columns = [
    {
      name: "Company Name",
      selector: (row) => row.company_name,
      sortable: true,
      className: "age",
    },
    {
      name: "First & Last Name",
      selector: (row) => row.first_name + " " + row.last_name,
      sortable: true,
      className: "age",
    },

    {
      name: "Email",
      selector: (row) => row.email,
      sortable: true,
      className: "age",
    },
    {
      name: "Phone Number",
      selector: (row) => row.phone,
      sortable: true,
      className: "age",
    },
    {
      name: "City",
      selector: (row) => row.city_step2,
      sortable: true,
      className: "age",
    },
    {
      name: "Action",
      cell: (row) => (
        <Link to={`#`} className="btn btn-primary">
          <FaEye />
        </Link>
      ),
      ignoreRowClick: true,
      allowOverflow: true,
      button: true,
    },
  ];

  const [searchQuery, setSearchQuery] = useState("");

  // Define filtered data based on the search query
  const filteredRecords = records.filter((record) =>
    Object.values(record)
      .join(" ")
      .toLowerCase()
      .includes(searchQuery.toLowerCase())
  );
  return (
    <div>
      <div className="d-flex align-items-start gap-0">
        <Sidebar />
        <div className="d-flex flex-column gap-0 w-100">
          <TopBar />
          <section className="dashboard_adminh">
            <div className="container-xl">
              <div className="row gy-4">
                <div className="col-12">
                  <div className="card p-3">
                    <h5 className="mb-3">Company</h5>
                    <div className="d-flex justify-content-end align-items-end">
                      <div class="search-bar">
                        <input
                          type="text"
                          placeholder="Search by name or email..."
                          value={searchQuery}
                          onChange={(e) => setSearchQuery(e.target.value)}
                        />
                        <span class="search-icon">
                          <svg
                            xmlns="http://www.w3.org/2000/svg"
                            fill="none"
                            viewBox="0 0 24 24"
                            stroke="currentColor"
                          >
                            <path
                              stroke-linecap="round"
                              stroke-linejoin="round"
                              stroke-width="2"
                              d="M21 21l-4.35-4.35M17 11a6 6 0 11-12 0 6 6 0 0112 0z"
                            />
                          </svg>
                        </span>
                      </div>
                    </div>
                    <DataTable
                      columns={columns}
                      data={filteredRecords}
                      pagination
                      highlightOnHover
                      striped
                      responsive
                      noDataComponent={
                        <div className="text-center">
                          <span>No users found</span>
                        </div>
                      }
                    />
                  </div>
                </div>
              </div>
            </div>
          </section>
        </div>
      </div>
    </div>
  );
}

export default AdminCompany;
